export default {
    data(){
        return {
            children: '',
            phone: '1232233332'
        }
    },
    methods: {
        getChildren(){
            for(let i = 0; i < 3; i++){
                this.children += `
                                <div id="container">
                                    <img id="avator" src="../assets/logo.png" alt="头像"/>
                                    <div id="info-box">
                                        <span>姓名:章三</span><br/>
                                        <span>年龄:6个月</span><br/>
                                        <span>vip有效期:2018年12月30日</span>
                                    </div>
                                    <div id="b-vip">马上开通</div>
                                </div>
                                `;
            }
        }
    },
    mounted(){
        this.getChildren();
    }
}



