export default {
    data(){
        return {
            phone: '135xxxx3322',
            sendMsg: '发送验证码',
            timerFinish: true,
        }
    },
    methods: {
        sendCode(){
            if(this.timerFinish){
                this.timerFinish = false;
                let count = 60;
                this.sendMsg = `请稍后${--count}s`;
                let timer = setInterval(()=>{
                    count--;
                    this.sendMsg = `请稍后${count}s`;
                    if(count <= 0){
                        clearInterval(timer);
                        this.timerFinish = true;
                        this.sendMsg = '发送验证码';
                    }
                },1000);
                //请求发送验证码
            }
        },
        change(){
            
        }
    },
    mounted(){
        
    }
}



