
import Confirm from 'vux/src/components/confirm/index.vue'

export default {
components: {
Confirm
},
data(){
return {
sendMsg: '发送验证码',
errorMsg: '',
timerFinish: true,
phoneNum: '',
code: '',
loadingText: '努力加载中',
showLoading: false,
showAlert: false,
showToast: false,
toastText: '操作失败'
}
},
methods: {
sendCode(){
if(this.timerFinish && this.phoneNum != ''){
this.timerFinish = false;
let count = 60;
this.sendMsg = `请稍后${--count}s`;
let timer = setInterval(()=>{
count--;
this.sendMsg = `请稍后${count}s`;
if(count <= 0){
clearInterval(timer);
this.timerFinish = true;
this.sendMsg = '发送验证码';
}
},1000);

this.$http.post("/wx/verify", {cellphone: this.phoneNum}, {
success: (data)=> {
console.log(data);
},
error: (error)=> {
console.log("error", error);
if(error.error){
this.toastText = error.error;
this.showToast = true;
}
}
})
}else{

}
},
login(){
if(this.code != ''){
this.showLoading = true;

this.$http.post("/wx/signin", {verify_code: this.code}, {
success: (data)=> {
console.log("success", data);
this.showLoading = false;
if(data.users && data.users.length > 0){
window.localStorage.setItem('phone', data.account.cellphone);
if(this.$router.history.current.query.redirect){
let redirect = this.$router.history.current.query.redirect;
this.$router.replace(redirect);
}else{
this.$router.replace('report');
}
}else{
this.showAlert = true;
}
},
error: (error)=> {
console.log("error", error);
this.showLoading = false;
this.toastText = error.error;
this.showToast = true;
}
});
}
},
onConfirm(){
this.$router.push('download');
},
configWx(data){
console.log(data);
this.$wx.config({
debug: true,
appId: data.appId,
timestamp: data.timestamp,
nonceStr: data.nonceStr,
signature: data.signature,
jsApiList: [

'onMenuShareTimeline','onMenuShareAppMessage'
]
})
}
},
mounted(){
let uri = encodeURIComponent(window.location.href);
let api = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx30cd7cdd85d5b554&redirect_uri=${uri}&response_type=code&scope=snsapi_userinfo&state=123&#wechat_redirect`;
window.location = api;















}
}



